<template>
	<div class="container-fluid bg-background">
		<div class="row">

			<div class="col-12">
				<div class="container text-center">
					<div class="row justify-content-center">
						<div class="col-12 p-2">
							<img class="d-block mx-auto" :src="logo_jingle">
						</div>
						<div class="col-12 p-2">
							<img class="d-block mx-auto" :src="logo_card">
						</div>
						<div class="col-12 col-lg-5">
							<div v-if="userName != ''">
								<h5 class="p-2 bg-black text-white d-block mx-auto w-auto">Olá {{ userName }}! Veja os jingles que criamos para você! </h5>
							</div>
						</div>
					</div>
				</div>
			</div>

			<div class="col-12" style="min-height: 450px;">
				<component :is="component" :players="players" :token_files="token_files"></component>
			</div>

		</div>

		<div class="row">
			<FooterTemplate/>
		</div>

	</div>
</template>

<script>

/* eslint-disable */

import FooterTemplate from './components/FooterComponent.vue'
import LoadingComponent from './components/LoadingComponent.vue'
import ListPlayersComponent from './components/ListPlayersComponent.vue'

export default {

	components: {FooterTemplate, LoadingComponent, ListPlayersComponent},

	data() {
		return {
			component:"LoadingComponent",
			token_files:"",
			userName:"",
			logo_jingle: process.env.VUE_APP_PREFIX+"/imagens/logo_jingleshopp.png",
			logo_card: process.env.VUE_APP_PREFIX+"/imagens/audiocard_logo.png",
			players: []
		}
	},

	async created() {
		// dados da API

		let uri = window.location.pathname
		let parts = uri.split('/')
		let tk = parts[parts.length -1 ].trim() != "" ? parts[parts.length -1 ].trim() : parts[parts.length -2 ].trim();

		let response = await fetch(process.env.VUE_APP_API+'card/getLinkedFiles', {
            method:'post',
            body: JSON.stringify({
                token:tk
            })
        })

		if(response.status != 200){
			return;
		}

		const res  = await response.json()
		const data = res.data
		this.userName    = data.pessoa.nome
		this.players     = data.files
		this.token_files = data.token_files
		this.component   = "ListPlayersComponent"

	}

}
</script>
