<template>
    <div class="container">
        <div class="row py-2 justify-content-center">
            <div class="col-12 col-lg-8 bg-black p-2" style="border-radius: 20px;">
                <div class="row p-0 m-0">
                    <div class="col-3 col-lg-2">
                        <div v-if="status == 'loading'">
                            <div id="loadingbtn" class="spinner-border text-primary mt-4" style="width: 3rem; height: 3rem;" role="status">
                                <span class="visually-hidden">Loading...</span>
                            </div>
                        </div>
                        <div v-if="status == 'paused'">
                            <button @click="play()" id="playbtn" class="mt-3" style="background-color:transparent; border: 0px;"><img :src="play_ic" style="border: 0px;"></button>
                        </div>
                        <div v-if="status == 'played'">
                            <button @click="pause()" id="pausebtn" class="btn mt-3" style="background-color:transparent; border: 0px;"><img :src="pause_ic" style="border:none"></button>
                        </div>
                        <!-- button play | button stop | loading -->
                    </div>
                    <div class="col-8 col-lg-9">
                        <div class="row">
                            <div class="col-12">
                                <p class="text-white text-start">{{ player.nome }}</p>
                            </div>
                            <div class="col-12">
                                <input @click="pause()" type="range" class="music-timeline" min="0" :max="player.seconds" v-model="time">
                            </div>
                            <div class="col-12">
                                <div class="row">
                                    <div class="col-5">
                                        <!-- timmer -->
                                        <span class="text-white" style="font-size:12px;">{{ timestr }} | {{ totaltimeStr }}</span>
                                    </div>
                                    <!--  -->
                                    <div :class="`${status == 'played' ? 'do-animation' : ''} col-3 text-center`">
                                        <div class="anim1 loadim"></div>
                                        <div class="anim2 loadim"></div>
                                        <div class="anim3 loadim"></div>
                                        <div class="anim4 loadim"></div>
                                        <div class="anim5 loadim"></div>
                                    </div>
                                    <div class="col-4">
                                        <input type="range" min="1" max="150" v-model="volume" class="volume" style="margin-top:5px; margin-left: 5px; float:right;">
                                        <img style="width: 15px; float:right; margin-top:3px;" :src="volume_ic" alt="">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-1">
                        <h1 class="text-white text-center" style="margin-top: 30px;">{{ itemK + 1 }}</h1>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    
    props: {
        player: Object,
        itemK: Number,
        token_files: String
    },
    
    data() {
        return {
            play_ic: process.env.VUE_APP_PREFIX+"/imagens/play_ic.png",
            pause_ic: process.env.VUE_APP_PREFIX+"/imagens/pause_ic.png",
            volume_ic: process.env.VUE_APP_PREFIX+"/imagens/volume_ic.png",
            status:'loading',
            time:0,
            timer:null,
            total_sec:0,
            volume:50,
            totaltimeStr: "00:00",
            timestr: "00:00",
            dataFile: "",
            source:null,
            clickedTime:false,
            bytes:[],
            audioCtx:null,
            timelineClicked: false,
            gainNode:null
        }
    },

    async created(){

        let response = await fetch(process.env.VUE_APP_API+'files/get', {
            method:'post',
            body: JSON.stringify({
                file:this.player.slug,
                token: this.token_files
            })
        })

        if(response.status != 200){
            return
        }

        let body = await response.json()
        this.dataFile = body.data

        this.audioCtx = new (window.AudioContext || window.webkitAudioContext)()

        var binary_string = window.atob(this.dataFile);
        var len = binary_string.length;
        let bytes = new Uint8Array(len);

        for (var i = 0; i < len; i++) {
            bytes[i] = binary_string.charCodeAt(i);
        }

        this.bytes = bytes.buffer
        
        this.source = this.audioCtx.createBufferSource();
        this.gainNode = this.audioCtx.createGain()
        
        this.audioCtx.decodeAudioData(
            this.bytes,
            (buffer) => {
                this.source.buffer = buffer;
                this.setTotalSec()
            },
            (err) => console.error(`Error with decoding audio data: ${err.err}`)
        )

    },

    methods: {
        calcTime(time){      
            let totalMin = time >= 60 ? Math.floor(time / 60) : 0;
            let totalSec = time < 60 ? time : time % 60;
            let timestr  = (totalMin < 10 ? `0${totalMin}`: String(totalMin))
                            +":"+
                           (totalSec < 10 ? `0${totalSec}`: String(totalSec))
            return timestr;
        },

        play(){

            var binary_string = window.atob(this.dataFile);
            var len = binary_string.length;
            let bytes = new Uint8Array(len);

            for (var i = 0; i < len; i++) {
                bytes[i] = binary_string.charCodeAt(i);
            }

            this.bytes = bytes.buffer
            
            this.source = this.audioCtx.createBufferSource();
            this.gainNode = this.audioCtx.createGain()
            
            this.audioCtx.decodeAudioData(
                this.bytes,
                (buffer) => {
                    this.source.buffer = buffer;
                    this.source.connect(this.audioCtx.destination);
                    this.source.loop = false;
                    this.source.start(0, this.time);
                    this.gainNode.gain.value = this.volume / 100
                    this.gainNode.connect(this.audioCtx.destination)
                    this.source.connect(this.gainNode)
                },
                (err) => console.error(`Error with decoding audio data: ${err.err}`)
            )
            
            this.status = 'played'
            
            this.timer = setInterval(() => {
                let este = parseInt(this.time);
                if(este > this.total_sec){
                    this.time = 0
                    this.pause()
                    return;
                }
                this.time = este + 1
            }, 1000)

        },

        pause(){
            this.source.stop(0)
            this.status = 'paused'
            clearInterval(this.timer)
        },

        setTotalSec(){
            if(this.total_sec == 0){
                this.total_sec = Math.floor(this.source.buffer.duration);
                this.totaltimeStr = this.calcTime(this.total_sec)
            } 
        }

    },

    watch: {
        dataFile(){
            this.status = 'paused'
        },
        time(n){
            this.time = n
            let totalMin = n >= 60 ? Math.floor(n / 60) : 0;
            let totalSec = n < 60 ? n : n % 60;
            this.timestr  = (totalMin < 10 ? `0${totalMin}`: String(totalMin))
                            +":"+
                           (totalSec < 10 ? `0${totalSec}`: String(totalSec))
        },
        volume(v){
            this.volume = v
            this.gainNode.gain.value = this.volume / 100
        }
    }
}
</script>

<style scoped>

    .loadim {
        width: 3px;
        margin-top: 8px;
        height: 2px;
        background-color:yellow;
        float: left;
        margin-left: 3px;
    }

    .do-animation .anim1 {
        animation: animate 0.3s infinite alternate;
        animation-delay: 0.1s;
    }
    
    .do-animation .anim2 {
        animation: animate 0.3s infinite alternate;
        animation-delay: 0.3s;
    }

    .do-animation .anim3 {
        animation: animate 0.3s infinite alternate;
        animation-delay: 0.2s;
    }

    .do-animation .anim4 {
        animation: animate 0.3s infinite alternate;
        animation-delay: 0.4s;
    }

    .do-animation .anim5 {
        animation: animate 0.3s infinite alternate;
        animation-delay: 0.0s;
    }

    @keyframes animate {
        from {
            height:2px;
            margin-top:8px;
        }
        to {
            height:10px;
            margin-top:0px;
        }
    }

    .music-timeline {
        width: 100%;
        background: black;
    }

    .music-timeline {
        height: 25px;
        -webkit-appearance: none;
        margin: 10px 0;
        width: 100%;
    }
    .music-timeline:focus {
        outline: none;
    }
    .music-timeline::-webkit-slider-runnable-track {
        width: 100%;
        height: 5px;
        cursor: pointer;
        animate: 0.2s;
        box-shadow: 0px 0px 0px #000000;
        background: yellow;
        border-radius: 1px;
        border: 0px solid #000000;
    }
    .music-timeline::-webkit-slider-thumb {
        box-shadow: 0px 0px 0px #000000;
        border: 1px solid black;
        height: 18px;
        width: 18px;
        border-radius: 25px;
        background: yellow;
        cursor: pointer;
        -webkit-appearance: none;
        margin-top: -7px;
    }
    .music-timeline:focus::-webkit-slider-runnable-track {
        background: yellow;
    }
    .music-timeline::-moz-range-track {
        width: 100%;
        height: 5px;
        cursor: pointer;
        animate: 0.2s;
        box-shadow: 0px 0px 0px #000000;
        background: yellow;
        border-radius: 1px;
        border: 0px solid #000000;
    }
    .music-timeline::-moz-range-thumb {
        box-shadow: 0px 0px 0px #000000;
        border: 1px solid yellow;
        height: 18px;
        width: 18px;
        border-radius: 25px;
        background: black;
        cursor: pointer;
    }
    .music-timeline::-ms-track {
        width: 100%;
        height: 5px;
        cursor: pointer;
        animate: 0.2s;
        background: black;
        border-color: black;
        color: transparent;
    }
    .music-timeline::-ms-fill-lower {
        background: yellow;
        border: 0px solid #000000;
        border-radius: 2px;
        box-shadow: 0px 0px 0px #000000;
    }
    .music-timeline::-ms-fill-upper {
        background: yellow;
        border: 0px solid #000000;
        border-radius: 2px;
        box-shadow: 0px 0px 0px #000000;
    }
    .music-timeline::-ms-thumb {
        margin-top: 1px;
        box-shadow: 0px 0px 0px #000000;
        border: 1px solid yellow;
        height: 18px;
        width: 18px;
        border-radius: 25px;
        background: black;
        cursor: pointer;
    }
    .music-timeline:focus::-ms-fill-lower {
        background: yellow;
    }
    .music-timeline:focus::-ms-fill-upper {
        background: yellow;
    }


    .volume {
        -webkit-appearance: none;
        width: 50%;
        height: 10px;
        background: gray;
        outline: none;
        opacity: 0.7;
        -webkit-transition: .2s;
        transition: opacity .2s;
    }

    .volume::-ms-fill-upper {
        background: yellow;
        border-radius: 2px;
    }

    .volume:hover {
        opacity: 1;
    }

    .volume::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 10px;
        height: 10px;
        background: yellow;
        cursor: pointer;
    }

    .volume::-moz-range-thumb {
        width: 10px;
        height: 10px;
        background: yellow;
        cursor: pointer;
    }

    .volume:focus::-ms-fill-lower {
        background: yellow;
    }
    .volume:focus::-ms-fill-upper {
        background: yellow;
    }

</style>